.has-focus .fake-input .value.selected {
  border-right: 1px solid black; }

.fake-input .value {
  font-size: 50px;
  border-right: 1px solid white;
  color: #ccc; }
  .fake-input .value.active {
    color: #000; }

.fake-input input {
  height: 0;
  left: -100px;
  opacity: 0;
  width: 1px;
  z-index: -99;
  top: -9999px;
  left: -9999px; }



